#posts-container{
    background-color: transparent;
    width: auto;
    height: auto;
    padding: 10px;
    margin-top: 10%;

    color: #3D3D3D;

    display: flex;
    flex-wrap: nowrap;
    column-gap: 20px;
    
    overflow-y: hidden;
    overflow-x: scroll;
    align-self: flex-start;
}

#posts-container::-webkit-scrollbar {
    display: none;
}

#posts-container .post {
    width: 300px;
    min-width: 300px;
    height: 400px;
    background-color:transparent;
    margin: 30px 0px;
    border-radius: 25px;
    border-top-left-radius: 4px;
    border-bottom-right-radius: 4px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;

    box-shadow: 1px 16px 12px 2px #3d3d3d71;

    cursor: pointer;

}

#posts-container .post .post-resume {
    width: 100%;
    height: 160px;
    padding: 15px;

    background-color: aliceblue;
    border-radius: 25px;
    border-top-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border: none;
    
    display: flex;
    flex-wrap: wrap;
    row-gap: 15px;
}

#posts-container .post .post-resume h3  {
    line-height: 1.5rem;
}

#posts-container .post .post-resume h3 > a {
    width: 100%;
    word-wrap: break-word;
    color: #3D3D3D;
}

#posts-container .post .post-resume h3 > a:hover {
    color: #3d3d3d;
}

#posts-container .post .post-resume .post-user {
    width: 100%;
    flex-grow: 2;
    font-size: 0.8rem;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 5px;
}

.post-user figure > img {
    width: 24px;
    height: 24px;
    vertical-align: middle;
    object-fit: cover;
    border-radius: 100%;
    background-color: aliceblue;
}

#posts-container .post .post-resume .post-user > a {
    font-weight: 600;
    color: #765CAE;
}

#posts-container .post .post-resume .post-user:link {
    color: #3D3D3D;
}

#posts-container .post .post-resume .category, .post-date {
    width: 50%;
    /* margin-top: 15px; */
    flex-grow: 1;
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.08rem;

}

#posts-container .post .post-resume .category {
   color: #765CAE;
   text-transform: uppercase;
}

/*  input */

.main-header {
    width: 100%;
    padding: 10px;
    display: flex;

    justify-content: space-between;
    align-items: center;
    column-gap: 20px;

    justify-content: space-between;
}

.main-header .search-icon {
}
.main-header > input {
    width: 60%;
    border-radius: 8px;
    border: none;
    padding: 5px;
    display: none;
}

.main-header > h1 {
    font-size: 1rem;
}

